* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
#root {
	display: flex;
	justify-content: center;
	font-family: 'Mukta', sans-serif;
	
}
body{
	background-color: #F5F5F5;
}
.App {
	width: 800px;
	margin-top: 30px;
}
.post {
	display: flex;
	padding: 15px;
	border: 2px solid teal;
	margin-top: 15px;
	justify-content: space-between;
	align-items: center;
}
h1,h2{
	font-weight: 500;
}
strong{
	font-weight: 600;
}